import "./mypage.css"

import localStorage from "localStorage";
import SingleContent from "../../components/SingleContent/SingleContent";

export default function Mypage() {

    var videoHistory = localStorage.getItem("videoHistory");
    if (videoHistory) {
        videoHistory = JSON.parse(videoHistory);
    } else {
        videoHistory = [];
    }

    return (
        <div className="mypageContainer">
            <div className="mypageTitle">
                观看历史~~~
            </div>
            <div className="mypage">
                {videoHistory.length === 0 ? "没有什么信息～～～" : videoHistory.map((c) => (
                    <SingleContent
                    key={c.slug}
                    slug={c.slug}
                    poster={c.poster}
                    title={c.title}
                    date={c.date}
                    duration={c.duration}
                    url={c.url}
                />
                ))}
            </div>
        </div>
    );
}
